@import "./reset.scss";
@import "./variables.scss";

.App {
  position: relative;
  max-width: 400px;
  margin: 0 auto;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;

  &__header {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: $header-height;
    background-image: url("./Images/pattern.png");
    background-size: cover;
  }

  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    text-align: center;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 800;
    color: rgb(61, 59, 59);
    text-transform: uppercase;

    margin-top: 20px;
  }
}

iframe {
  display: none;
}
